<template>
  <div
    class="progress-bar"
    ref="rootRef"
    @click="onClick">
    <div class="bar-inner">
      <div
        class="progress"
        :style="progressStyle"
        ref="progressRef"></div>
      <div
        class="progress-btn-wrapper">
        <div
          class="progress-btn"
          :style="btnStyle"
          @touchstart.prevent="onTouchStart"
          @touchmove.prevent="onTouchMove"
          @touchend.prevent="onTouchEnd"></div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { computed, defineEmits, defineProps, reactive, ref, toRefs, watch } from 'vue'

const props = defineProps({
  progress: {
    type: Number,
    default: 0
  }
})

const emit = defineEmits(['progress-changing', 'progress-changed'])

const progressBtnWidth = 16
const rootRef = ref(null)
const progressRef = ref(null)
const offset = ref(0)
const { progress } = toRefs(reactive(props))

const progressStyle = computed(() => {
  return `width:${offset.value}px`
})
const btnStyle = computed(() => {
  return `transform:translate3d(${offset.value}px,0,0)`
})

watch(progress, (newProgress) => {
  const barWidth = rootRef.value.clientWidth - progressBtnWidth
  offset.value = barWidth * newProgress
})

const touch = {}
const onTouchStart = (e) => {
  touch.x1 = e.touches[0].pageX
  touch.beginWidth = progressRef.value.clientWidth
}
const onTouchMove = (e) => {
  const delta = e.touches[0].pageX - touch.x1
  const tempWidth = touch.beginWidth + delta
  const barWidth = rootRef.value.clientWidth - progressBtnWidth
  const progress = Math.min(1, Math.max(tempWidth / barWidth, 0))
  offset.value = barWidth * progress
  emit('progress-changing', progress)
}
const onTouchEnd = () => {
  const barWidth = rootRef.value.clientWidth - progressBtnWidth
  const progress = progressRef.value.clientWidth / barWidth
  emit('progress-changed', progress)
}

const onClick = (e) => {
  const rootRefVal = rootRef.value
  const rect = rootRefVal.getBoundingClientRect()
  const offsetWidth = e.pageX - rect.left
  const barWidth = rootRefVal.clientWidth - progressBtnWidth
  const progress = offsetWidth / barWidth
  emit('progress-changed', progress)
}

</script>

<style lang="scss" scoped>
.progress-bar {
  height: 30px;

  .bar-inner {
    position: relative;
    top: 13px;
    height: 4px;
    background: rgba(0, 0, 0, 0.3);

    .progress {
      position: absolute;
      height: 100%;
      background: $color-theme;
    }

    .progress-btn-wrapper {
      position: absolute;
      left: -8px;
      top: -13px;
      width: 30px;
      height: 30px;

      .progress-btn {
        position: relative;
        top: 7px;
        left: 7px;
        box-sizing: border-box;
        width: 16px;
        height: 16px;
        border: 3px solid $color-text;
        border-radius: 50%;
        background: $color-theme;
      }
    }
  }
}
</style>
