<template>
  <div class="singer-detail">
    <music-list :songs="songsList" :title="title" :pic="pic" :loading="loading"></music-list>
  </div>
</template>

<script setup>
import { onMounted, defineProps, ref, computed } from 'vue'
import { getSingerDetail } from '../service/singer'
import { processSongs } from '../service/song'
import MusicList from '../components/music-list/MusicList'
import storage from 'good-storage'
import { SINGER_KEY } from '../assets/js/constant'
import { useRoute, useRouter } from 'vue-router'

const props = defineProps({
  singer: Object
})
const songsList = ref([])
const loading = ref(true)
const route = useRoute()
const router = useRouter()
const computedSinger = computed(() => {
  let ret = null
  const singer = props.singer
  if (singer) {
    ret = singer
  } else {
    const cachedSinger = storage.session.get(SINGER_KEY)
    if (cachedSinger && cachedSinger.mid === route.params.id) {
      ret = cachedSinger
    }
  }
  return ret
})
const pic = computed(() => computedSinger.value && computedSinger.value.pic)
const title = computed(() => computedSinger.value && computedSinger.value.name)
onMounted(async () => {
  if (!computedSinger.value) {
    const path = route.matched[0].path
    router.push({
      path
    })
    return
  }
  const result = await getSingerDetail(computedSinger.value)
  const songs = await processSongs(result.songs)
  songsList.value = songs
  loading.value = false
})
</script>

<style lang="scss" scoped>
.singer-detail {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: $color-background;
}
</style>
